body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.search-form {
  text-align: center;
  margin: 20px 0;
}

input[type="text"] {
  padding: 10px;
  font-size: 16px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #3498db;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
}

.gallery {
  max-width: 1200px;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 0 auto;
  display: flex;
}

.photo-card {
  width: 300px;
  margin: 20px;
  transition: transform .3s ease-in-out;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.photo-card:hover {
  transform: scale(1.05);
}

img {
  width: 100%;
  height: auto;
}

.info {
  padding: 10px;
}

.info-item {
  color: #333;
  margin: 5px 0;
  font-size: 14px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #2ecc71;
  border: none;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: block;
}

.load-more:hover {
  background-color: #27ae60;
}

